import { Component, ViewChild } from '@angular/core';

import { ChildComponentNew } from '../child-new/child-new.component';

@Component({
  selector: 'app-parentNew',
  template: `
    <button (click)="onAddForChildNew()">Add++</button>
    <app-childtNew></app-childtNew>
  `,
})

export class ParentComponentNew {
  @ViewChild(ChildComponentNew)
  mychild: ChildComponentNew;
  
  onAddForChildNew() {
    this.mychild.value++
  }
}