import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-words',
  templateUrl: './words.component.html',
  styleUrls: ['./words.component.css']
})
export class WordsComponent implements OnInit {
  filterStatus = 'xem_tat_ca';
  newEn = '';
  newVn = '';
  showButton = false;
  arrWords = [
    { id: 1, en: 'action', vn: 'hành động', memorized: true },
    { id: 2, en: 'actor', vn: 'diễn viên', memorized: false },
    { id: 3, en: 'activity', vn: 'hoạt động', memorized: true },
    { id: 4, en: 'active', vn: 'chủ động', memorized: true },
    { id: 5, en: 'bath', vn: 'tắm', memorized: false },
    { id: 6, en: 'bedroom', vn: 'phòng ngủ', memorized: true }
  ];
  constructor() { }

  ngOnInit() {
  }
  pushWords() {
    this.arrWords.push({
      id: this.arrWords.length + 1,
      en: this.newEn,
      vn: this.newVn,
      memorized: false
    });
    this.newEn = '';
    this.newVn = '';
  }
  hideButton() {
    this.showButton = !this.showButton;
  }
  remove(id: number) {
    const index = this.arrWords.findIndex(word => word.id === id);
    this.arrWords.splice(index, 1);
  }
  getStatus(memorized: boolean) {
    const XemTatCa = this.filterStatus === 'xem_tat_ca';
    const TuDaNho = this.filterStatus === 'tu_da_nho' && memorized;
    const TuChuaNho = this.filterStatus === 'tu_chua_nho' && !memorized;
    return XemTatCa || TuDaNho || TuChuaNho
  }
}
