import { Injectable } from "@angular/core";
import { Http } from "@angular/http";

@Injectable()

export class AppWeatherNowService {
  constructor(private http: Http) {}

  getTemp(cityName: string) :any {
    const Url = 'http://api.openweathermap.org/data/2.5/weather?units=metric&APPID=2a54b1f72e3539615c3d42e47785c9ec&q=' + cityName;
    return this.http.get(Url)
    .toPromise()
    .then(res => res.json())
    .then(resJson => resJson)
  }
}