import { Component,OnInit } from '@angular/core';
import { AppWeatherNowService } from './app-weather-now.service';

@Component({
  selector: 'app-app-weather-now',
  templateUrl: './app-weather-now.component.html',
  styleUrls: ['./app-weather-now.component.css'],
  providers: [AppWeatherNowService]
})
export class AppWeatherNowComponent implements OnInit {
  nameCity: string = '';
  temp: number;
  valueSearch: string = '';
  loadingSearch: string = '';
  loadingShow: boolean = true;

  constructor(private appWeatherNowService: AppWeatherNowService) {}

  ngOnInit() {}

  searchTempCity() {
    this.loadingShow = false;
    this.loadingSearch = 'Loading ...';
    this.appWeatherNowService.getTemp(this.valueSearch)
    .then(temp => {
      this.temp = temp.main.temp;
      this.nameCity = temp.name;
      this.loadingSearch = '';
      this.loadingShow = true;
      this.valueSearch = '';
    })
    .catch(err => {
      alert('Not found');
      this.valueSearch = '';
      this.loadingShow = true;
    });
  }
}
  