import { Component } from '@angular/core';
import { FormValidService } from './form-invalid.service';

@Component({
  selector: 'app-sign-in',
  styleUrls: ['./form-invalid.component.css'],
  templateUrl: './form-invalid.component.html',
  providers: [FormValidService]
})

export class FormInvalidComponent {

  constructor( private formValidService: FormValidService ) {}

  submit(fornSignIn) {
    this.formValidService.postData(fornSignIn.value)
    .then(resJson => console.log(resJson))

    console.log('Component')
  }
}